/** @jsx jsx */
import { jsx } from '@emotion/core';
import { useMemo } from 'react';
import formatDistanceToNow from 'date-fns/formatDistanceToNow';
import koLocale from 'date-fns/locale/ko';
import { Card, CardHeader, CardContent, Avatar, Typography, makeStyles, CardActionArea } from '@material-ui/core';
import AnimatedListItem from 'components/Animated/AnimatedListItem';

interface Props {
  link: string;
  title: string;
  authorProfile: string;
  authorName: string;
  content: string;
  pubDate: string;
}
const BlogFeedCard = ({ link, title, authorProfile, authorName, content, pubDate }: Props) => {
  const ellipsedContent = useMemo(() => {
    return `${content.substring(0, 100)}...`;
  }, [content]);
  const publishedDate = useMemo(() => {
    return formatDistanceToNow(new Date(pubDate), {
      addSuffix: true,
      locale: koLocale,
    });
  }, [pubDate]);

  const style = makeStyles({
    title: {
      fontWeight: 'bold',
    },
    link: {
      textDecoration: 'none',
    },
  })();

  return (
    <AnimatedListItem>
      <a href={link} target="_blank" rel="noopener noreferrer" className={style.link}>
        <Card>
          <CardActionArea>
            <CardHeader
              avatar={<Avatar src={authorProfile} alt={authorName} />}
              title={authorName}
              subheader={publishedDate}
            />
            <CardContent>
              <Typography gutterBottom={true} variant="subtitle1" component="h3" className={style.title}>
                {title}
              </Typography>
              <Typography variant="caption">{ellipsedContent}</Typography>
            </CardContent>
          </CardActionArea>
        </Card>
      </a>
    </AnimatedListItem>
  );
};

export default BlogFeedCard;
