import { useEffect, useState, useCallback } from 'react';
import { flattenDepth } from 'lodash';
import { BlogFeed, Member } from 'src/models';
import { setLocalStorageItem, getLocalStorageItem } from 'src/utils/localStorage';
import { BLOG_CACHE_KEY, BLOG_FEED_CACHE_EXPIRY } from 'src/constants';
import { fetchMemberFeeds } from 'src/utils';
import { useAllMembers } from 'src/hooks/useAllMembers';

async function fetchAllFeeds(members: Member[]): Promise<BlogFeed[]> {
  const jobs = members.filter(member => member.rss).map(member => fetchMemberFeeds(member));
  const response = await Promise.all(jobs);

  return flattenDepth(response, 2).sort((a, b) => new Date(b.pubDate).getTime() - new Date(a.pubDate).getTime());
}

export function useBlogFeeds(): { feeds: BlogFeed[]; isFetched: boolean } {
  const [feeds, setFeeds] = useState<BlogFeed[]>([]);
  const [isFetched, setIsFetched] = useState(false);
  const members = useAllMembers();

  const fetch = useCallback(async () => {
    try {
      const cachcedFeeds = getLocalStorageItem<BlogFeed[]>(BLOG_CACHE_KEY);
      if (cachcedFeeds && cachcedFeeds.length > 0) {
        setFeeds(cachcedFeeds);
        setIsFetched(true);
        return;
      } else {
        const feeds = await fetchAllFeeds(members);
        setFeeds(feeds);
        setIsFetched(true);
        setLocalStorageItem(BLOG_CACHE_KEY, feeds, BLOG_FEED_CACHE_EXPIRY);
      }
    } catch (e) {
      setFeeds([]);
    }
  }, [members]);

  useEffect(() => {
    if (typeof window === 'undefined') {
      return;
    }
    fetch();
  }, [fetch]);

  return {
    feeds,
    isFetched,
  };
}
