/** @jsx jsx */
import { jsx } from '@emotion/core';
import Page from 'components/Layout/Page';
import BlogFeedCard from 'components/Cards/BlogFeedCard';
import Loading from 'components/Utils/Loading';
import { Grid, Container } from '@material-ui/core';
import { useBlogFeeds } from 'hooks/useBlogFeeds';

const Blogs = () => {
  const { feeds, isFetched } = useBlogFeeds();

  return (
    <Page pageName="Blogs" description="루비콘 멤버들의 개인 블로그에 업데이트 되는 포스팅들" showTitle={true}>
      <Container>
        {isFetched ? null : <Loading text="루비콘 멤버들의 블로그 포스팅을 불러오는 중 입니다..." />}
        <Grid container spacing={3}>
          {feeds.map((feed, index) => {
            return (
              <Grid item key={index} xs={12} sm={6} md={4} lg={3}>
                <BlogFeedCard
                  title={feed.title}
                  link={feed.link}
                  authorProfile={feed.author.profileImg.childImageSharp.fixed.src}
                  authorName={feed.author.name.en}
                  content={feed.contentSnippet}
                  pubDate={feed.pubDate}
                />
              </Grid>
            );
          })}
        </Grid>
      </Container>
    </Page>
  );
};

export default Blogs;
